


























































import { Component, Vue } from "vue-property-decorator";
import MainCanvas from "./components/MainCanvas.vue";
import ReadOnlyCanvas from "./components/ReadOnlyCanvas.vue";
import { MakeTable } from "./components/makeTable";
import DisplayTable from "./components/DisplayTable.vue";

interface Pos {
  x: number;
  y: number;
}

interface PosText {
  x: number;
  y: number;
  text: string;
}

interface AllJoin {
  line: number[];
  and: number[];
  or: number[];
  not: number[];
  input: number[];
  output: number[];
}

interface Parts {
  lines: number[][];
  ands: number[][];
  ors: number[][];
  nots: number[][];
  inputName: PosText[];
  inputs: number[];
  outputName: PosText[];
  outputs: number[];
  dots: AllJoin[];
}

@Component({
  components: {
    MainCanvas,
    ReadOnlyCanvas,
    DisplayTable
  }
})
export default class App extends Vue {
  selected = "";
  partsName = ["line", "and", "or", "not", "dot", "input", "output"];
  inputText = "x";
  outputText = "a";
  showTable = false;
  dotsFormula: string[] = [];
  dotsBool: boolean[][] = [];
  loading = false;

  parts: Parts = {
    lines: [],
    ands: [],
    ors: [],
    nots: [],
    inputName: [],
    inputs: [],
    outputName: [],
    outputs: [],
    dots: []
  };

  poses = {
    lines: [],
    ands: [],
    ors: [],
    nots: [],
    inputs: [],
    outputs: [],
    dots: []
  };

  doMakeTable() {
    this.loading = true;
    const tableData = MakeTable(this.parts);
    this.showTable = true;
    this.dotsFormula = tableData.formula;
    this.dotsBool = tableData.bool;
  }

  resetSelected() {
    this.selected = "";
  }

  setParts(parts: Parts) {
    this.parts = parts;
  }

  setPoses(poses) {
    this.poses = poses;
  }
}
